import React, { Component } from "react";
import gsap from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";

class AboutSection extends Component {
  constructor(props) {
    super(props);
    this.trigger = null;
    this.image = null;
    this.content = null;
  }
  state = {};
  componentDidMount() {
    gsap.registerPlugin(ScrollTrigger);
    gsap.set(this.content, { translateX: 300, opacity: 0 });
    gsap.set(this.image, { translateX: -300, opacity: 0 });
    gsap.to([this.image, this.content], {
      scrollTrigger: {
        trigger: this.trigger,
        start: "top center",
        toggleActions: "play reverse resume reset",
      },
      opacity: 1,
      translateX: 0,
      duration: 1.5,
    });
  }
  render() {
    return (
      <section
        ref={(div) => (this.trigger = div)}
        className="section about"
        id="about"
      >
        <div className="container">
          <h2 className="section-title">A propos</h2>
          <div className="about-me">
            <img
              ref={(div) => (this.image = div)}
              className="about-img"
              src="./img/me.png"
              alt="me"
            ></img>
            <div
              ref={(div) => (this.content = div)}
              className="about-me-text-container"
            >
              <p className="about-me-text">
                Je m'appelle Kevin, je suis un développeur passionné par les
                technologies de toutes sortes, mais surtout celles du
                web.J’adore apprendre de nouvelles choses, de nouvelles choses
                technologies, expérimenté également, c'est en essayant que l'on
                avance
              </p>
            </div>
          </div>
        </div>
      </section>
    );
  }
}

export default AboutSection;
