import React, { Component } from "react";
class Project extends Component {
  state = {};
  render() {
    let { cover, title, id, description, link } = this.props.info;
    return (
      <div className="col-md-10 offset-md-1" key={id}>
        <article className="project-container">
          <figure style={{ backgroundImage: `url(${cover})` }}></figure>
          <div className="project-content">
            <h1 className="project-title">{title}</h1>
            <p className="project-description">{description}</p>
            <div className="project-controls">
              <a
                href={link}
                target="_blank"
                rel="noopener noreferrer"
                className="project-controls-btn"
              >
                Voir la demo
              </a>
              {/* <button className="project-controls-btn">Voir la demo</button> */}
            </div>
          </div>
        </article>
      </div>
    );
  }
}

export default Project;
