import React, { Component } from "react";
import Skill from "./Skill";
import gsap, { TimelineLite } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
let skills = {
  front: [
    {
      name: "HTML",
      img: "html",
    },
    {
      name: "CSS",
      img: "css",
    },
    {
      name: "JS",
      img: "js",
    },
    {
      name: "Boostrap",
      img: "bootstrap",
    },
    {
      name: "Jquery",
      img: "jquery",
    },
    {
      name: "Sass",
      img: "sass",
    },
    {
      name: "React",
      img: "react",
    },
    {
      name: "Git",
      img: "git",
    },
  ],
  back: [
    {
      name: "PHP",
      img: "php",
    },
    {
      name: "symfony",
      img: "symfony",
    },
    {
      name: "JS",
      img: "js",
    },
    {
      name: "node JS",
      img: "node",
    },
  ],
};

class Skills extends Component {
  constructor(props) {
    super(props);
    this.skill = [];
    this.trigger = null;
    this.myTween = new TimelineLite({ paused: true });
  }
  state = {};
  componentDidMount() {}
  render() {
    return (
      <section
        ref={(section) => (this.trigger = section)}
        className="section tech"
        id="tech"
      >
        <div className="container">
          <h2 className="section-title --black">Les technologies</h2>
          <div className="row">
            <div className="col-lg-5">
              <div className="tech-front">
                <p className="tech-cat">Front end</p>
                <div className="row">
                  {skills.front.map((e, index) => (
                    <div
                      className="col-4"
                      ref={(skill) => (this.skill[index] = skill)}
                    >
                      <Skill name={e.name} img={e.img} key={e.name} />
                    </div>
                  ))}
                </div>
              </div>
            </div>
            <div className="offset-lg-1 col-lg-5">
              <p className="tech-cat">Back end</p>
              <div className="tech-back">
                <div className="row">
                  {skills.back.map((e, index) => (
                    <div
                      ref={(skill) =>
                        (this.skill[this.skill.length + index] = skill)
                      }
                      className="col-4"
                    >
                      <Skill name={e.name} img={e.img} key={e.name} />
                    </div>
                  ))}
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    );
  }
}

export default Skills;
